.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes cube-rotate {
    from {
      transform: rotateX(0deg);
    }
    to {
      transform: rotateX(360deg);
    }
  }
  

scary-cube {
    width: 100%;
    height: 20vh;
    margin-bottom: 10px;
    /* animation: cube-rotate infinite 20s linear; */
}

.graph > div,
.graph > div > div,
.graph canvas {
    height: 100vh;
}

.graph {
    width: 100%;
    height: 100vh;
}

.grid {
    position: relative;
    z-index: 5000;
}

body {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
}

.cubegraph-buttons {
    position: fixed;
    z-index: 6000;
    right: 20px;
    top: 20px;
} 